:root {
    --primarycolor: #7ED957; /* azul */
    --secondarycolor: #ffffff; /* blanco */
    --thirdcolor: #faf8ff; /* gris */
    --fourthcolor: #008037; /* azul clarito */
    --contrastcolor: #ee6464; /* rojo */
}

.containerStore{
  padding: 50px;
}

.column1 {
    flex: 40%;
    padding: 20px;
    height: 300px;
  }
 .container_bnt{
   /* box fixed */
   box-sizing: border-box;
   border-radius: 12px;
   box-shadow: 0px 35px 50px rgba( 0, 0, 0, 0.2 );
  background-color: #fff;
  }
 .container{
  /* box fixed */
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.column2 {
    flex: 60%;
    padding: 10px;
    height: 300px;
   
  }
  
  /* Clear floats after the columns */
  .row {
    display: flex;
    flex-wrap: wrap;

  }
  .info{
    padding-left: 19px;
  }

  
  .btn{
  display: block;
  margin: 10px 0;
  width: 210px;
  height: 80px;
  border: none;
  background-color:var(--primarycolor);
  color: #fff;
  font-size: 18px;
  border-radius: 15px;
  outline: none;
  cursor: pointer;
  transition: .3s linear;

}

.btn2{
  display: block;
  margin: 10px 0;
  width: 150px;
  height: 30px;
  border: none;
  background-color:var(--primarycolor);
  color: #fff;
  font-size: 18px;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  transition: .3s linear;
  margin-left: 50px;
}

.btn1:hover {
  transform: rotateX(14deg);
  box-shadow: 0 2px 2px #008037;
}



/* Table Styles */

.table-wrapper{
  margin: 20px 20px 30px;
  box-shadow: 0px 15px 20px rgba( 0, 0, 0, 0.2 );
}

.fl-table {
  border-radius: 5px;
  font-size: 12px;
  font-weight: normal;
  border: none;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  white-space: nowrap;
  background-color: white;
  overflow: auto;
}

.fl-table td, .fl-table th {
  text-align: center;
  padding: 8px;
}

.fl-table td {
  border-right: 1px solid #f8f8f8;
  font-size: 12px;
}

.fl-table thead th {
  color: #000;
  background: var(--primarycolor);
}


.fl-table thead th:nth-child(odd) {
  color: #000;
  background: var(--fourthcolor);

}

.fl-table tr:nth-child(even) {
  background: #F8F8F8;
}

/* Responsive */

@media (max-width: 767px) {
  .fl-table {
      display: block;
      width: 100%;
  }
  .table-wrapper:before{
      content: "Scroll horizontally >";
      display: block;
      text-align: right;
      font-size: 11px;
      color: white;
      padding: 0 0 10px;
      overflow: hidden;
  }
  table {
      width:100%;
  }
  thead {
      display: none;
  }
  tr:nth-of-type(2n) {
      background-color: inherit;
  }
  tr td:first-child {
      background: #f0f0f0;
      font-weight:bold;
      font-size:2.5em;
  }
  tbody td {
      display: block;
      text-align:center;
  }
  tbody td:before {
      content: attr(data-th);
      display: block;
      text-align:center;
  }

  .fl-table tbody {
      width: auto;
      position: relative;
      overflow-x: auto;
  }
  
  .fl-table tbody tr {
      display: table-caption;
  }
  
  .fl-table tbody tr:nth-child(odd) {
      background: none;
  }

  .fl-table tr:nth-child(even) {
      background: transparent;
  }
 
  .fl-table tr td:nth-child(odd) {
      background: #F8F8F8;
  }
 
  .fl-table tbody td {
      display: block;
      text-align: center;
  } 
  .btn{
    width: 150px;
    height: 60px;

  }

}

/* Sortable tables */
table.sortable thead {
  background-color:#eee;
  color:#666666;
  font-weight: bold;
  cursor: default;
}
